section {
    .projects {
      flex-direction: column;
      justify-content: center;
  
      .projects-container {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
        grid-gap: 24px;
        width: 100%;
  
        @media screen and (max-width: 769px) {
          padding: 0;
          grid-template-columns: 1fr;
        }
      }
    }
  }
  